*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    background-color: #ddd;
    margin-bottom: 400px;
}
.main{
    width: 1058px;
    margin: 0 auto;
    background-color: #fff;
}
/*______________________________________________Nav Section*/

nav{
    height: 60px;
    color: #111;
    background-color: #f0f0f0;
}
nav .navbar{
    overflow: hidden;
    align-items: center;
    padding: 15px 25px;
    font-size: 16px;
    display: flex;
}

nav .logo i{
    font-size: 25px;
    padding-right: 10px;
    cursor: pointer;
}
nav h1{
    cursor: pointer;
}
nav h1 span{
    color: #3bca83;
}
nav a{
    text-decoration: none;
    color: #111;
}

nav .search{
    position: relative;
    left: 50px;
    width: 350px;
}
nav .search i{
    font-size: 16px;
    padding: 5px;
    color: #ddd;
    background-color: #111;
}
nav input{
    padding: 5px 15px;
    width: 300px;
}
nav .search i:hover{
    background-color: #3bca83;
}
nav .menu{
    position: relative;
    left: 450px;

}
.navbar .navbar-items{
    display: flex;
    list-style-type: none;
    font-size: 16px;
    cursor: pointer;
}
.navbar-items li a{
    text-decoration: none;
    padding: 10px;
    color: #111;
}
.navbar-items li a:hover{
    color: #3bca83;
}
.navbar .navbar-link i{
    padding: 0 8px;
}
.navbar-items .center a{
    position: relative;
    right: 160px;
}
.navbar-items .green a{
    border-radius: 20px;
    background-color: #3bca83;
}
.navbar-items .green a:hover{
    color: #ddd;
}
/*_________________________________________________Login Popup*/
.popup-form {
    position: absolute;
    left: 50%;
    top: 50%;
    height: 70%;
    transform: translate(-50%, -50%);
    /* display: none; */
    position: fixed;
    z-index: 10;
    background-color: #ddd;
}
.popup-form .form-container{
    max-width: 450px;
    padding: 15px;
}
.popup-form .form-container h2{
    text-align: center;
    font-size: 50px;
    padding: 75px 15px;
    

}
.popup-form .form-container input[type=text]{
    width: 100%;
    padding: 15px;
    margin: 5px 0 22px 0;
    border: 1px solid #f1f1f1;
    background-color: #fff;
}
.popup-form .form-container input[type=tel]{
    width: 100%;
    padding: 15px;
    margin: 5px 0 22px 0;
    border: 1px solid #f1f1f1;
    background-color: #fff;
}
.popup-form .form-container input:focus,
.popup-form .form-container input:hover{
    background-color: #f1f1f1;
    outline: none;
}
.popup-form .login-btn{
    background-color: dodgerblue;
    color: white;
    padding: 16px 20px;
    cursor: pointer;
    margin-bottom: 40px;
    border: none;
    opacity: 0.8;
    width: 100%;
    outline:none;
    font-size: 16px;
}
.popup-form .login-btn:hover{
    opacity: 1;
}
.popup-form .form-container i{
    opacity: 0.8;
    font-size: 32px;
    position: absolute;
    right: 10px;
    top: 3px;
    color: #444;
}
.popup-form .form-container i:hover{
    color: black;
}
/*_____________________________________________Header Section*/
#header{
    height: 250px;
    background-image: url("./images/banner.jpg");
}
#header h2{
    position: relative;
    text-align: center;
    color: #ddd;
    top: 30px;
}
#header h2 span{
    font-size: 3.2rem;
    color: #3bca83;
    padding: 10px;
}
#header-0{
    height: 300px;
    background-image: url("./images/background-map.jpg");
}
#header-0 .search{
    position: relative;
    left: 310px;
    top: 30px;
    padding: 8px;
    width: fit-content;
    align-content: center;
    background-color: #f0f0f0;
}
#header-0 .search i{
    font-size: 25px;
    padding: 5px 15px;
    color: #111;
    cursor: pointer;
}
#header-0 .search select:active,
#header-0 .search i:active{
    border-style: none;
    outline: none;
}
#header-0 input{
    padding: 8px 15px;
    width: 500px;
    border-radius: 20px;
    border-style: none;
    outline: none;

}
#header-0 select{
    padding: 5px 15px;
    width: auto;
    font-size: 16px;
    border-style: none;
    cursor: pointer;
    background-color: #f0f0f0;
}
#header-0 .search i:hover{
    color: #3bca83;
}
#header-0 .text{
    position: relative;
    left: 320px;
    top: 60px;
}
#header-0 h2{
    color: #3bca83;
    font-size: 2.4rem;
    padding: 10px 0;
}
#header-0 p{
    color: #ddd;
    font-size: 1.3rem;
}
#header-0 button{
    color: #111;
    font-size: 1rem;
    padding: 5px 20px;
    margin-top: 20px;
    border-radius: 15px;
    cursor: pointer;
}
#header-0 button a{
    text-decoration: none;
    color: #111;
}
#header-0 button i{
    padding: 5px 5px 5px 25px;
}
#header-0 button:hover{
    background-color: #3bca83;
}
#header-1{
    height:150px;
    background-image: url("./images/bg_buy.jpg");
    background-size: cover;
}
#header-2{
    width: 100%;
    height: 550px;
    overflow: hidden;

}
#header-2 .screen img{
    filter: brightness(0.5);
}
#header-2 .info{
    position: absolute;
    top: 80px;
    color: white;
    padding: 10px 20px;
    line-height: 25px;
}

#header-2 .info h1{
    padding: 30px 0;
    font-size: 50px;
}
#header-2 .info p span{
    padding:0 5px;
    margin: 5px;
}
#header-2 .info p span:nth-child(odd){
    background-color: #3bca83;

}
#header-2 .screen .options{
    position: absolute;
    top: 60%;
    color: white;
}
#header-2 .screen .options i{
 font-size: 2.6rem;
 padding: 20px;
 margin: 0 40px;
 border-radius: 50%;
 background-color: #f0f0f02a;
}
#header-2 .screen .options span{
    padding: 0 50px;
}
#header-2 .screen .options i:hover{
    color: #3bca83;
}
#header-2 .screen .options span:nth-child(even){
    position: relative;
    left: 190px;
/* margin: 80px; */
}
    

/*________rating____________*/
 .star-wrapper {
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    direction: rtl;
  }
 .star-wrapper a {
    font-size: 2em;
    color: #fff;
    text-decoration: none;
    transition: all 0.5s;
    margin: 4px;
  }
 .star-wrapper a:hover {
    color: gold;
    transform: scale(1.3);
  }
 .s1:hover ~ a {
    color: gold;
  }
  .s2:hover ~ a {
    color: gold;
  }
  .s3:hover ~ a {
    color: gold;
  }
  .s4:hover ~ a {
    color: gold;
  }
  .s5:hover ~ a {
    color: gold;
  }



/*____________________________________________Main Section*/
#section{
    height: 70vh;
}
#section .container{
    width: 70%;
    height: 40vh;
    top: 25%;
    left: 15%;
    padding: 11px 5px;
    position: relative;
}
#section .container ul{
    list-style: none;
    font-size: 1.2rem;
    width: 100%;
    height: 100%;
    background-color: #fff;
}
#section .container ul li {
    padding: 25px;
    cursor: pointer;
    text-decoration: none;
    border: 1px solid #888;
}
#section .container a{
    text-decoration: none;
    color: #111;
    list-style: none;

}
#section .container ul li:hover{
    color: #3bca83;

}
#section .container ul li:hover{
    transform: translate(6px);
}
#section .container li i:nth-child(even){
    position: absolute;
    right: 30px;
}

#section-0{
    height: auto;

    background-color: #efefef;
}
#section-0 aside{
    
    position: relative;
    top: -285px;
    left: 15px;
    max-width: 250px;
    height: auto;
    display: inline-block;
}
#section-0 .sidebar li{
    height: 60px;
    font-size: 18px;
    list-style:none;
    padding: 22px 30px 22px 15px;
    background: #fffffffb;
}
#section-0 .sidebar a{
    text-decoration: none;
    color: #111;
}


#section-0 .sidebar li:hover{
    border-top: 2px solid #948e8e;
    border-bottom: 2px solid #948e8e;
    background-color: #3bca83;
    color: white;
}
#section-0 .sidebar a i{
    float: right;
    padding: 10px 15px;
    margin: -10px 0;
    background-color: #948e8e23;
    border-radius: 60%;
}
#services{
    width: 75%;
    height: auto;
    float: right;
}
#services .box-title{
    width: 100%;
    padding: 35px 0 10px 50px;
}
#services .box-title p{
    font-family: 'Trebuchet MS', sans-serif;
}
#services .container{
    display: flex;
    height: fit-content;
    width: 100%;
    flex-wrap: wrap;
    padding: 5px 40px;
}
#services .container .service-box{
    flex: 1;
    height: 180px;
    min-width: 334px;
    background-color: white;
    margin: 10px;
    overflow: hidden;
    border-radius: 5px;
}
#services .container .service-box:hover{
    transform: scale(1.04);
}

.left{
    float: left;
    width: 50%;
}
.right{
    float: right;
    width: 50%;
}
#services .service-box a{
    text-decoration: none;
}
#services .service-box ul{
    position: relative;
    left: 18px;
}
#services .service-box li{
    padding: 6px;
    list-style-type: none;
    color: #111;
}
#services .service-box li:hover{
    color: #3bca83;
}
#section-1{
    min-height: 100vh;
    background-color: #f0f0f0;
}
#section-1 .container{
    display: flex;
}
#section-1 .title{
    align-items: center;
    display: flex;
    padding: 10px 25px;
    background-color: white;
}
#section-1 .title .separator{
    margin: 0 8px;
}
#section-1 .title a{
    text-decoration: none;
    color: #111; 
}
#section-1 .title a:hover{
    color: #3bca83; 
}

#section-1 aside .sorting{
    width: 258px;
    height: 600px;
    padding: 0 20px;
}
#section-1 aside .sorting h2{
    padding: 22px 0 15px 0;
    border-bottom: 1px solid  #444;
}
#section-1 aside .sorting li{
    margin-top: 6px;
    padding: 5px;
    list-style: none;
    font-size: 18px;
    cursor: pointer;
}
#section-1 aside .sorting li:hover{
    background-color: #3bca83;
}

#section-1 main .results{
    flex: 1;
    overflow: auto;
    width: 800px;
    min-height: 100vh;
    padding: 10px;
    background-color: #f0f0f0;
}
#section-1 main .results .item-box{
    width:100%;
    height: 220px;
    margin-bottom: 10px;
    background-color: white;
    display: flex;
}
#section-1 main .results .item-img{
    width: 250px;
    height: 100%;
}
#section-1 main .results .info{
    /* position: relative; */
    width: 360px;
    padding: 10px 20px;
    line-height: 25px;

}
#section-1 main .info h1{
    padding: 10px 0;
}
#section-1 main .info p span{
    padding:0 5px;
    margin: 5px;
}
#section-1 main .info p span:nth-child(odd){
    background-color: #3bca83;

}
#section-1 main .info button{
    font-size: 16px;
    margin: 20px 0;
    padding: 8px 35px;
    background-color: #3bca83;
    cursor: pointer;
}
#section-1 main .info a{
    text-decoration: none;
    color: #111;
}
#section-1 main .verify{
    position:relative;
    top: 40%;
}

#section-3{
    height:30vh;
    width: 100%;
}

#error{
    display: table;
    width: 100%;
    height: 60vh;
    text-align: center;
    transition: all 0.6s;


}
#error .fof{
    display: table-cell;
    vertical-align: middle;
    color: #888;

}

.fof h1{
    font-size: 50px;
    display: inline-block;
    padding-right: 12px;
}
.fof p{
font-size:60px;
}
.fof Link, .fof a{
    padding-top: 50px;
}


/*______________________________________________Footer Section*/
.footer{
    min-height: 40vh;
    display: flex;
    background-color: #f0f0f0;
}

.footer .about{
    position: relative;
    top: 50px;
    left: 30px;
    width: 60%;
    flex: 1;
    line-height: 1.6rem;
}
.footer .about p{
    padding: 15px 0 0 30px;
}

.footer .newsletters{
    position: relative;
    width: 40%;
    top: 50px;
    left: 20px;
}
.footer .about h2, .footer .about h4,
.footer .newsletters h2{
    color: #3bca83;
}
.footer .newsletters input{
padding: 5px;
font-size: 1rem;
}
.footer .newsletters i{
    position: relative;
    top: 6px;
    font-size: 2rem;
    padding: 0 8px;
    color:#fff;
    background-color: #398f39;
    cursor: pointer;
}
.footer .newsletters .social i{
    color: #444;
    top: 50px;
    padding: 2px;
    background-color: #f0f0f0;
}
.footer .newsletters .social i:hover{
    color: #398f39;
}
